.authorization-form-content{
    margin: 0 auto 0;
    max-width: 360px;
}

.authorization-header{
    font-size: 22px;
    font-family: 'Product Sans', 'Roboto', sans-serif;
    padding: 22px 0;
    text-overflow: ellipsis;
    overflow: hidden;
}

.authorization-header-content{
    word-wrap: break-word;
    white-space: nowrap;
}

.authorization-actions{
    text-align: center;
    display: flex;
}